
  @import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');


.input-form{
  width: 50%;
  position: relative;
 
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 25px;
  top: 100px;
  left: 100px;
} 

  

.input-form h1{
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 64px;
}
.input-form input{
    font-family: 'Inter', sans-serif;
    width: 290px;
    height: 50px;
    font-weight: 400;
    font-size: 24px;
    background: transparent;
    padding-left: 12px;
    border: 2px solid white;
    border-radius: 5px;
    color: white;
}
.input-form input::placeholder{
   color:  #FFFFFF;
}
.buttonBox{
    width: 290px;
    display: flex;
    flex-wrap: wrap;
    border: none;
    justify-content: space-between;
    
}
.buttonBox button{
    font-family: 'Inter', sans-serif;
    font-weight: 300;
    width: 61px;
    height: 61px;
    font-size: 24px;
    cursor: pointer;
    border-radius: 5px;
}

.output, .msg{
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    }
